import React from 'react';
import {Box , MainPhoto} from 'style/stylePages';

const photoDB = [
  {
    id : 0,
    name : " "
  },
  {
    id : 1,
    name : " "
  },
  {
    id : 2,
    name : " "
  },
  {
    id : 3,
    name : " "
  },
  {
    id: 4,
    name : " "
  }
]


const Photo = () => {
    let photoBox = photoDB.map(
      (photo, info) => (
        <Box id = {photo.id} key = {info}>
          {photo.name}
        </Box>
      )
    )

    return (
        <MainPhoto>
            {photoBox}
        </MainPhoto>
    );
};

export default Photo;



