import React, {Component} from 'react';
import { MainMenuList, MainMenu , Title} from 'style/styleComponents';
import Menu from './Menu';

let MenuDB = [
  {
    name: "FILM",
    english: "",
    china: "",
    children: [
      "photo",
      "video"
    ]
  },
  {
    name: "FASHION",
    english:  "",
    china: "",
    children: [
      "top",
      "bottom",
      "acc"
    ]
  },
  {
    name: "title"
  },
  {
    name: "ART",
    english: "",
    china: "",
    children: [
      "goods",
      "book"
    ]
  },
  {
    name: "CONTACT",
    english: "",
    china: "",
    children: []
  }
]

class MainBar extends Component {

  static defaultProps = {
    lang: "english"
  };

  render() {
    const ManyMenu = MenuDB.map(
        (menu, info) => {
          if(menu.name === "title"){
            return(
              <MainMenu key ={info}>
                <Title to={process.env.PUBLIC_URL}>
                  <img src={require('img/floor23.png')} width='65px' alt='title'/>
                </Title>
              </MainMenu>
            )
          }
          else {
            return(
              <Menu
                name={menu.name}
                children={menu.children}
                lang={this.props.lang === "english" ? menu.english : menu.china}
                key={info}
              />
            )
          }
        }
    )

    return(
      <MainMenuList>
        { ManyMenu }
      </MainMenuList>
    )
  }
}

export default MainBar;



